import React, { useState } from 'react';
import { connect } from 'react-redux';
import {
  Grid,
  InputAdornment,
  Button,
  TextField,
  CircularProgress
} from '@material-ui/core';
import Alert from '@material-ui/lab/Alert';
import MailOutlineTwoToneIcon from '@material-ui/icons/MailOutlineTwoTone';
import LockTwoToneIcon from '@material-ui/icons/LockTwoTone';
import { Formik } from 'formik';
import * as Yup from 'yup';
import { withNamespaces } from 'react-i18next';

import { CP_GATE, CP_URL } from '../../../helpers/settings';
import { loginUser } from '../../../reducers/Auth';

const Login = (props) => {
  const t = props.t;
  const [callProgress, setCallProgress] = useState(false);
  const [state, setState] = useState({
    message: '',
    showMessage: false,
    messageType: ''
  });

  const userLogin = async (data) => {
    setCallProgress(true);
    try {
      const resp = await loginUser(data);
      setState({
        message: 'Login Successful',
        showMessage: true,
        messageType: 'success'
      });
      setCallProgress(false);
      window.location.replace(`${CP_GATE}?token=${resp.result}`);
    } catch (error) {
      setState({
        message: 'Unable to login',
        showMessage: true,
        messageType: 'error'
      });
      setCallProgress(false);
      closeMessage();
    }
  };

  const closeMessage = () => {
    setTimeout(()=>{
      setState({
        message: '',
        showMessage: false,
        messageType: ''
      })
    }, 5000)
  }

  return (
    <>
      <Formik
        initialValues={{
          email: '',
          password: ''
        }}
        validationSchema={Yup.object({
          password: Yup.string()
            .min(6, t('Must be at least 6 characters'))
            .required(t('Required')),
          email: Yup.string()
            .email('Invalid email address')
            .required(t('Required'))
        })}
        onSubmit={userLogin}>
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit
        }) => {
          return (
            <form onSubmit={handleSubmit}>
              <div className="py-4">
                <div className="text-center">
                  <p className="font-size-lg mb-0 text-black-50">
                    {t(
                      'Fill in the fields below to login to your account'
                    )}
                  </p>
                </div>
                <div>
                  <div className="my-4">
                    <TextField
                      fullWidth
                      variant="outlined"
                      id="email"
                      name="email"
                      label={t('Email address')}
                      InputProps={{
                        startAdornment: (
                          <InputAdornment position="start">
                            <MailOutlineTwoToneIcon />
                          </InputAdornment>
                        )
                      }}
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.email}
                      error={touched.email ? Boolean(errors.email) : false}
                      helperText={touched.email && errors.email}
                    />
                  </div>
                  <div className="mb-3">
                    <TextField
                      fullWidth
                      variant="outlined"
                      id="password"
                      name="password"
                      label={t('Password')}
                      type="password"
                      InputProps={{
                        startAdornment: (
                          <InputAdornment position="start">
                            <LockTwoToneIcon />
                          </InputAdornment>
                        )
                      }}
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.password}
                      error={touched.password ? Boolean(errors.password) : false}
                      helperText={
                        touched.password && errors.password
                      }
                    />
                  </div>
                  {state.showMessage && (
                    <div className="text-center py-4">
                      <Alert severity={state.messageType}>
                        {t(state.message)}
                      </Alert>
                    </div>
                  )}
                  <div className="text-center py-4">
                    <Button
                      disabled={callProgress}
                      type="submit"
                      className="btn-second font-weight-bold w-50 my-2">
                      {callProgress ? (
                        <CircularProgress size={20} />
                      ) : (
                          t('Log In')
                        )}
                    </Button>
                  </div>
                  <div className={props.language === 'ar-ae' ? 'text-left' : 'text-right'}>
                        <a href={`${CP_URL}/ForgotPassword`}>{t('Forgot Password')}</a>
                  </div>
                </div>
              </div>
            </form>
          );
        }}
      </Formik>
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    // Auth: state.Auth
    language: state.language
  };
};

//connect with redux and give the access to defined method
export default withNamespaces()(
  connect(mapStateToProps, { })(Login));
